<template>
  <div>
    <vSearch @getOption="getOption" @searchInfo="searchInfo" @clearSearchInfo="clearSearchInfo" :pageType="pageType"></vSearch>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="675"
      @sort-change="sortChange"
      >
      <el-table-column
        prop="customerName"
        label="客户名称"
        sortable>
      </el-table-column>
      <el-table-column
        prop="customerNo"
        label="客户号"
        sortable>
      </el-table-column>
      <el-table-column
        label="被买断仓单">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="openDialog(scope.row, 'warehouse')"
            type="text"
            size="small">
            查看
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        prop="totalLoan"
        label="总放款金额（人民币）"
        sortable>
        <template slot-scope="scope">
          {{tofixed(scope.row.totalLoan)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="originalPrice"
        label="原仓单总价值（人民币）"
        sortable>
        <template slot-scope="scope">
          {{tofixed(scope.row.originalPrice)}}
        </template>
      </el-table-column>
      <el-table-column
        prop="totalCurrentPrice"
        label="现仓单总价值（人民币）"
        sortable>
        <template slot-scope="scope">
          {{tofixed(scope.row.totalCurrentPrice)}}
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        layout="prev, pager, next, jumper"
        :total="totalPage">
      </el-pagination>
    </div>
    <el-dialog title="被买断仓单" :visible.sync="dialogFormVisible" style="width:1500px">
      <div style="float:left">仓单编号：</div>
      <el-autocomplete
        class="selectSearch"
        v-model="state"
        :fetch-suggestions="querySearchAsync"
        placeholder="请输入内容"
        @select="handleSelect">
      </el-autocomplete>
      <el-button type="primary" @click="searchWarehouse" style="margin-top: -5px;">搜索</el-button>
      <el-table
      :data="warehouseList"
      style="width: 100%"
      max-height="675"
      >
        <el-table-column
          prop="wwNo"
          label="仓单编号"
          sortable
          width="180">
        </el-table-column>
        <el-table-column
          label="仓单现价/原价（人民币）"
          :formatter="priceFormatter"
          sortable
          width="300">
        </el-table-column>
        <el-table-column
          prop="contractNo"
          label="合同编号"
          sortable
          width="180">
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>

<script>
import vSearch from '../components/Search.vue';
import * as ajaxUrl from '../ajax.config.js';// 引入接口地址

export default {
  data() {
    return {
      pageType: 3,
      searchForm: {
        option: '',
        inputText: '',
      },
      currentCustomer: '',
      searchParam:{},
      currentPage: 1,
      pageSize: 0,
      totalPage: 0,

      dialogFormVisible: false,

      tableData: [],
      warehouseList: [],
      timeout: null,
      state: "",
    }
  },
  mounted(){
    this.searchInfo();
  },
  methods: {
    getOption(value, inputText){
      this.searchForm.option = value;
      this.searchForm.inputText = inputText;
    },
    searchInfo(orderField, orderType){
      this.searchParam[this.searchForm.option] = this.searchForm.inputText; 
      this.axios.get(ajaxUrl.GETCUSTOMERDATALIST, {
        params: {
          pageSize: this.pageSize,
          pageNum: this.currentPage,
          search: this.searchParam,
          orderField: orderField,
          orderType: orderType
        }
      }).then(res => {
        this.tableData = res.data.resData;
        this.totalPage = res.data.page.total;
        this.pageSize = res.data.page.pageSize;
        this.searchParam = {};
      }).catch(err => {
        console.log(err)
      })
    },
    clearSearchInfo(){
      this.searchForm.option = '';
      this.searchForm.inputText = '';
    },
    openDialog(row, type){
      this.dialogFormVisible = true;
      this.axios.get(ajaxUrl.GETCUSTOMERDATAWAREHOUSE, {
        params: {
          customerNo: row.customerNo
        }
      }).then(res => {
        this.warehouseList = res.data.resData;
        this.currentCustomer = row.customerNo;
      }).catch(err => {

      })
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.searchInfo();
    },
    priceFormatter(row, column){
      return this.tofixed(row.amount * row.goodsPrice) + " / " + this.tofixed(row.originalPrice)
    },
        //远程搜索 下拉搜索功能
    querySearchAsync(queryString, cb) {
      let warehouseList = this.warehouseList;
      for (let i of warehouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
      let results = queryString
        ? warehouseList.filter(this.createStateFilter(queryString))
        : warehouseList;

      clearTimeout(this.timeout);
      this.timeout = setTimeout(() => {
        cb(results);
      }, 300 * Math.random());
    },
    createStateFilter(queryString) {
      return state => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    handleSelect(item) {
      this.axios.get(ajaxUrl.GETCUSTOMERDATAWAREHOUSE, {
        params: {
          wwNo: this.state,
          customerNo: this.currentCustomer
        }
      }).then(res => {
        this.warehouseList = res.data.resData;
      }).catch(err => {
        console.log(err);
      })
      // this.warehouseList = item;
    },
    loadWarehouseId() {
      for (let i of this.warehouseList) {
        i.value = i.wwNo; //将想要展示的数据作为value
      }
    },
    searchWarehouse(){
      this.axios.get(ajaxUrl.GETCUSTOMERDATAWAREHOUSE, {
        params: {
          wwNo: this.state,
          customerNo: this.currentCustomer
        }
      }).then(res => {
        this.warehouseList = res.data.resData;
      }).catch(err => {
        console.log(err);
      })
    },
    //排序
    sortChange(params) {
      if(params.order == 'ascending'){
        this.searchInfo(params.prop, 'asc')
      }else if(params.order == 'descending'){
        this.searchInfo(params.prop, 'desc')
      }else{
        this.searchInfo()
      }
    },
    //
    tofixed(number) {
      return this.common.toFixed(number);
    }
  },
  components: {
    vSearch
  }
}
</script>

<style>
.pagination {
  text-align: center;
}
</style>



